@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.home-container {
  position: relative;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("/SC_Intromp4.mp4") no-repeat center fixed;
  background-size: cover;
}
.video-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1; 
}

.home-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); 
  z-index: 1; 
}

.home-page-title {
  position: relative; 
  z-index: 2;
  color: white;
  font-family: "Orbitron", sans-serif;
  text-align: center;
  padding: 0 20px;
}
.home-content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  z-index: 2;
}
.home-container h3{
  margin-top: 3rem;
  font-family: "Raleway", sans-serif;
}
.btn-53,
.btn-53 *,
.btn-53 :after,
.btn-53 :before,
.btn-53:after,
.btn-53:before {
  border: 0 solid;
  box-sizing: border-box;
}

.btn-53 {
  -webkit-tap-highlight-color: transparent;

  background-color: #000;
  background-image: none;
  color: #fff;
  cursor: pointer;
  font-family: ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont,
    Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif,
    Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
  font-size: 100%;
  line-height: 1.5;
  margin: 0;

  padding: 0;
  margin-top: 2rem;
}

.btn-53:disabled {
  cursor: default;
}

.btn-53:-moz-focusring {
  outline: auto;
}

.btn-53 svg {
  display: block;

}

.btn-53 [hidden] {
  display: none;
}

.btn-53 {
  border: 1px solid;
  border-radius: 999px;
  box-sizing: border-box;
  display: block;
  font-weight: 900;
  overflow: hidden;
  padding: 1.2rem 3rem;
  position: relative;
  text-transform: uppercase;
}

.btn-53 .original {
  background: #fff;
  color: #000;
  display: grid;
  inset: 0;
  place-content: center;
  position: absolute;
  transition: transform 0.2s cubic-bezier(0.87, 0, 0.13, 1);
}

.btn-53:hover .original {
  transform: translateY(100%);
}




.btn-53:hover span {
  opacity: 1;
  transform: translateY(0);
}


.btn-53 .letters {
  display: inline-flex;
  gap: 0.05em; /* add spacing */
}

.btn-53 span {
  opacity: 0;
  transform: translateY(-15px);
  transition: transform 0.3s cubic-bezier(0.87,0,0.13,1), opacity 0.3s;
}

.btn-53:hover span:nth-child(1) { transition-delay: 0s; }
.btn-53:hover span:nth-child(2) { transition-delay: 0.05s; }
.btn-53:hover span:nth-child(3) { transition-delay: 0.1s; }
.btn-53:hover span:nth-child(4) { transition-delay: 0.15s; }
.btn-53:hover span:nth-child(5) { transition-delay: 0.2s; }
.btn-53:hover span:nth-child(6) { transition-delay: 0.25s; }
.btn-53:hover span:nth-child(7) { transition-delay: 0.3s; }
.btn-53:hover span:nth-child(8) { transition-delay: 0.35s; }
.btn-53:hover span:nth-child(9) { transition-delay: 0.4s; }
.btn-53:hover span:nth-child(10) { transition-delay: 0.45s; }
.btn-53:hover span:nth-child(11) { transition-delay: 0.5s; }
.btn-53:hover span:nth-child(12) { transition-delay: 0.55s; }
.btn-53:hover span:nth-child(13) { transition-delay: 0.6s; }
.btn-53:hover span:nth-child(14) { transition-delay: 0.65s; }
.btn-53:hover span:nth-child(15) { transition-delay: 0.7s; }
.btn-53:hover span:nth-child(16) { transition-delay: 0.75s; }
.btn-53:hover span:nth-child(17) { transition-delay: 0.8s; }
.btn-53:hover span:nth-child(18) { transition-delay: 0.85s; }

@media (max-width: 685px) {
  .home-container .home-content h3 {
    font-size: 1rem;
  }
  .btn-53 {
    padding: 1rem 2.5rem;
    font-size: 0.9rem; 
  }
}
@media (max-width: 585px) {
  .home-container .home-content h3 {
    font-size: 15px;
  }
}
@media (max-width: 538px) {
  .home-container .home-content h3 {
    font-size: 13px;
  }
  .home-page-title{
    font-size: 1.5rem;
  }
  .btn-53 {
    padding: 0.8rem 2rem;
    font-size: 0.8rem;
  }
  h2{
    font-size: 1.2rem;
  }
}

